@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);

.movies .movie {
    width: 46%;
    background-color: #f9f7f7;
    color: #3f72af; 
    font-weight: 300;

    margin-bottom: 75px;
    padding: 20px;
    position: relative;
    border-radius: 5px;


    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie a {
    text-decoration: none;

    display: flex;

    justify-content: space-between;


}

.movie img {
    max-width: 170px;
    width: 100%;
    margin-right: 30px;

    position: relative;
    top: -50px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);

}

.movie .movie__title, .movie .movie__year {
    margin: 0;
    font-weight: 300;
}

.movie .movie__title {
    color: #3f72af;
    margin-top: 10px;
    margin-bottom: 5px;
    font-size: 1.6rem;
    font-weight: 600 ;
}

.movie .movie__year, .movie .movie__genres {
    color: #112d4e;
    opacity: 0.5;
} 

.movie .movie__summary {
    color: #3f72af; 
    opacity: 0.7;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0;

 
}

.movie__genres li {
    margin-right: 10px;
    font-size: 1rem;

}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Source Sans Pro', sans-serif;
    background-color: #dbe2ef;
    color: #222;

    height: 100%;
}

html, 
body,
.container {
    height: 100%;

    display: flex;
    justify-content: center;

}


.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.loader .loader__text {
    font-weight: 500;
    font-size: 1.5rem;
    align-items: center;

    color: #1b262c;
}


/* flex-wrap : 나열된 요소들의 총 넓이가 부모 넓이 보다 클 때  
               요소들을 다음줄에 이어서 나열 함 */
.movies {

    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 1.5rem;
    padding-top: 70px;

    width: 90%;

}


.navigation__container {
    margin: 1rem;
    
    width: 62px;
    background-color: #fff;

    border-radius: 5px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15), 0 3px 6px rgba(0,0,0,0.23);
    
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.linked {
    text-decoration: none;
    font-weight: 600;

    display: flex;
    flex-direction: column;
    color: #204051;
    
    padding: 5px;
    letter-spacing: 1.5px;

    
}

